.form-charge{
    .charge-type-group{
        margin-top: 40px;
        padding-top: 40px;
        border-top: 1px solid #F4F4F4;
    }
    .charge-type{
        .select-item{
            float: left;
            margin-right: 40px;
            width: 160px;
            border: 1px solid #D9D9D9;
            line-height: 42px;
            font-size: 18px;
            text-align: center;
            cursor: pointer;
            &.active{
                color: #FB3D51;
                border-color: #FB3D51;
            }
        }
    }
    .fake-select{
        .checked-bank{
            display: inline-block;
            position: relative;
            border: 1px solid #FB3D51;
            &::after{
                content: " ";
                position: absolute;
                width: 20px;
                height: 20px;
                right: 0;
                bottom: 0;
                z-index: 1;
                background: url(../img/checked-mark.png) no-repeat;
            }
        }
        a.btn-bank-more{
            display: inline-block;
            margin-left: 20px;
            color: #44a8f7;
        }
    }

}